import React from 'react';
import { graphql, Link } from 'gatsby';
import Img from 'gatsby-image';

import Layout from '../components/Layout';

import * as styles from '../styles/projectPage.module.css';

export default function ProjectPage({ data }) {
  
  const { 
    title, 
    stack, 
    date, 
    team, 
    deploy_link: deployUrl,
    github_repo: repo } = data.details.frontmatter;
  const { html } = data.details;

  const soloIcon = "../person-black-18dp.svg";
  const teamIcon = "../group-black-18dp.svg";
  
  return (
    <Layout>
        <div className="topSpacing"></div>
        <section>
          <div>
            <h2 className={styles.header}>{title}</h2>
            <h3 className={styles.subtitle}>{date.split('T')[0]}</h3>

            <div className={styles.links}>
              <img className={styles.icon} src="../link-black-18dp.svg" alt="Link" />
              <Link to={deployUrl}>Depl<span>o</span>yed Project</Link>
              <img className={styles.icon} src="../github.png" alt="GitHub" />
              <Link to={repo}><span>G</span>itHub Repository</Link>
            </div>
          </div>
        </section>

        <section className={styles.content}>

          <Img fluid={data.projectImage.childImageSharp.fluid} alt="Project"/>

          <div className={styles.stack}>
            <img className={styles.icon} src={team === "group" ? teamIcon : soloIcon} alt="Team Project" />
            <h3>Built using {stack}</h3>
          </div>
          <div className={styles.md} dangerouslySetInnerHTML={{__html: html}}/>
        </section>
    </Layout>
  );
}

export const query = graphql`
  query Project($slug: String) {
    details: markdownRemark(frontmatter: {slug: {eq: $slug}}) {
      frontmatter {
        date
        deploy_link
        description
        github_repo
        stack
        team
        title
      }
      html
    }
    projectImage: file(relativePath: {eq: "project_placeholder.jpg"}) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`